      <p class="art">{{item.article}}</p>

      <comment :msg="msg" />
    </div>
  </div>
</template>

<script>
import comment from './comment.vue'
export default {
  name: 'card',
  props: {
    item: Object,
    msg: Object,
    sideflag: Boolean
  },
  components: { comment },
  methods: {
    showmeaaa() {
      this.$emit('showdetails', this.item)
    }
  }
}
</script>

