          } else if (随机数 < 20) {
            let 句子 = this.来点名人名言()
            章节长度 = 章节长度 + 句子.length
            章节 = 章节 + 句子
          } else {
            let 句子 = this.来点论述(title)
            章节长度 = 章节长度 + 句子.length
            章节 = 章节 + 句子
          }
        }
        章节 = this.增加段落(章节)
        文章.push(章节)
      }
      return 文章.join('\n')
    }
  },
  created() {
    for (const t of this.title) {
      let p = {}
      p.title = t
      p.article = this.生成文章(t)
      this.pushArticle({ type: 'articles', data: p })
      this.pushArticle({ type: 'article', data: p })
    }
  }
}
</script>
