    right: 10px;
    position: absolute;
    background: rgba(0, 255, 200, 0.35);
    padding: 1em;
    border-radius: 10px;
    width: 250px;
    clip-path: circle(8% at 92% 16%);
    transition: all 0.5s ease-in-out;
    cursor: pointer;
  }
  .inner:hover {
    clip-path: circle(75%);
    background: rgba(1, 183, 255, 0.35);
  }
  .inner:hover span {
    color: rgba(255, 255, 255, 0);
  }
  .inner span {
    color: white;
    float: right;
    font-weight: bold;
    transition: all 0.5s;
    position: relative;
    margin-right: 2%;
  }

  .inner h1 {
    color: white;
    margin: 0;
  }
  .inner p {
    color: white;
    font-size: 0.8rem;
  }
}
