<template>
  <div class="topbar">
    <img src="../../assets/golove.svg" alt class="logo" />
    <div class="caley">
      <router-link v-for="(item, n) in calegrey" :key="n" :to="item.link">{{ item.title }}</router-link>
    </div>

    <div class="logBtn">
      <div class="search">
        <svg @click="mkSearch(search)">
          <use xlink:href="#search" />
        </svg>
        <input @keyup.enter="mkSearch(search)" type="text" name="search" v-model="search" class="search" />
      </div>

      <button class="loginbtn">登录</button>
      <!-- <button>注册</button> -->
    </div>
    <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="display: none">
      <symbol viewBox="0 0 1024 1024" id="search">
        <title>search</title>
        <path
          d="M883.626667 823.04l-145.066667-144.64A337.92 337.92 0 0 0 810.666667 469.333333a341.333333 341.333333 0 1 0-341.333334 341.333334 337.92 337.92 0 0 0 209.066667-72.106667l144.64 145.066667a42.666667 42.666667 0 0 0 60.586667 0 42.666667 42.666667 0 0 0 0-60.586667zM213.333333 469.333333a256 256 0 1 1 256 256 256 256 0 0 1-256-256z"
          fill="#ffffff"
        />
      </symbol>
    </svg>
  </div>
</template>

<script>
export default {
  props: { calegrey: Array },
  data() {
    return {
      search: ''
    }
  },
  watch: {
    search(val) {
      if (!val) {
        this.mkSearch(val)
      }
    }
  },
  methods: {
    mkSearch(val) {
      let value = val.replace(/\s*/g, '')
      if (value) {
        let res = this.$store.state.articles.filter(e => {
          if (e.title.indexOf(value) > -1 || e.article.indexOf(value) > -1) {
            return true
          }
        })
        this.$store.commit('setArticle', { type: 'articles', data: res })
      } else {
        this.$store.commit('setArticle', { type: 'articles', data: this.$store.state.article })
      }
    }
  }
}
</script>

<style lang="less">
.topbar {
  height: 60px;
  width: 100vw;
  background: var(--bgColor);
  backdrop-filter: var(--blur);
  img {
    position: absolute;
    height: 55px;
    left: 25%;
    margin-right: 60px;
  }
  .caley {
    width: 500px;

    position: absolute;
    left: 35%;
    router-link {
      cursor: pointer;
      text-decoration: none;
      text-align: center;
      display: inline-block;
      font-size: 20px;
      line-height: 60px;
      font-weight: bold;
      color: var(--color2);
      // margin: 0 12px;
      padding: 0 18px;
      // letter-spacing: 6px;
      height: 60px;
      // text-shadow: rgb(23, 59, 73) 2px 1px 2px;
      transition: all 0.15s ease-out;
    }
    .router-link-exact-active {
      background: rgba(255, 255, 255, 0.35);
      color: var(--color);
      transition: all 0.15s ease-in;
    }
  }
  .logBtn {
    position: absolute;
    right: 25%;
    display: flex;
    height: 60px;
    .search {
      margin-right: 10px;
      svg {
        opacity: 0.7;
        cursor: pointer;
        position: absolute;
        width: 25px;
        height: 25px;
        top: 0px;
        bottom: 0;
        right: 110px;
        margin: auto;
        z-index: 3;
      }
      input.search {
        padding: 0 8px;
        height: 35px;
        width: 85px;
        transition: width 0.15s ease;
        text-decoration: none;
        border: none;
        content: '';
        border-radius: 17.5px;
        margin-top: 12.5px;
        background: rgba(0, 132, 255, 0.4);
        backdrop-filter: var(--blur);
        color: var(--bgColor);
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        // &:focus {

        // }
        &:focus {
          text-decoration: none;
          border: none;
          outline: none;
          padding: 0 16px;
          width: 260px;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
        }
      }
    }
    .loginbtn {
      background: linear-gradient(215.43deg, #baff74 -12.36%, rgba(1, 154, 81, 0.77) 140.45%);
      border-radius: 20px;
      height: 40px;
      padding: 0 20px;
      font-size: 20px;
      text-shadow: rgba(23, 59, 73, 0) 2px 1px 2px;
      margin-top: 10px;
    }
  }
}
</style>